"use client";
import { useEffect, useState } from "react";
import Link from "next/link";
import { supabase } from "@/lib/supabaseClient";

export default function TableroTecnico() {
  const [tareas, setTareas] = useState([]);
  const [user, setUser] = useState(null);

  async function cargarUsuario() {
    const { data: { user } } = await supabase.auth.getUser();
    setUser(user);
    return user;
  }

  async function cargarTareas(uid) {
    const { data } = await supabase
      .from("tareas")
      .select("*, ordenes(*), maquinas(*)")
      .eq("tecnico_id", uid);

    setTareas(data || []);
  }

  useEffect(() => {
    (async () => {
      const u = await cargarUsuario();
      if (u) cargarTareas(u.id);
    })();
  }, []);

  const columnas = {
    pendiente: tareas.filter(t => t.estado === "pendiente"),
    proceso: tareas.filter(t => t.estado === "proceso"),
    finalizado: tareas.filter(t => t.estado === "finalizado"),
  };

  async function moverTarea(id, nuevoEstado) {
    await supabase.from("tareas").update({ estado: nuevoEstado }).eq("id", id);
    cargarTareas(user.id);
  }

  const renderColumna = (titulo, lista, estado) => (
    <div className="w-full bg-gray-100 p-3 rounded-lg">
      <h2 className="text-lg font-bold mb-3 text-gray-700">{titulo}</h2>
      <div className="space-y-3">
        {lista.map(t => (
          <div
            key={t.id}
            className="bg-white p-3 rounded shadow cursor-pointer hover:bg-gray-50"
          >
            <Link href={`/tecnico/tarea/${t.id}`}>
              <div>
                <p className="font-bold">{t.titulo}</p>
                <p className="text-sm text-gray-500">
                  Máquina: {t.maquinas?.codigo || "—"}
                </p>
              </div>
            </Link>

            {/* Botones rápidos de mover */}
            <div className="flex gap-2 mt-2">
              {estado !== "pendiente" && (
                <button className="text-xs bg-gray-300 px-2 py-1 rounded"
                  onClick={() => moverTarea(t.id, "pendiente")}
                >Pend</button>
              )}

              {estado !== "proceso" && (
                <button className="text-xs bg-blue-300 px-2 py-1 rounded"
                  onClick={() => moverTarea(t.id, "proceso")}
                >Proc</button>
              )}

              {estado !== "finalizado" && (
                <button className="text-xs bg-green-300 px-2 py-1 rounded"
                  onClick={() => moverTarea(t.id, "finalizado")}
                >Fin</button>
              )}
            </div>
          </div>
        ))}
      </div>
    </div>
  );

  return (
    <div className="p-6 grid grid-cols-1 md:grid-cols-3 gap-6">
      {renderColumna("Pendiente", columnas.pendiente, "pendiente")}
      {renderColumna("En proceso", columnas.proceso, "proceso")}
      {renderColumna("Finalizado", columnas.finalizado, "finalizado")}
    </div>
  );
}
